}

@media screen and (max-width: 899px) {
  .about img {
    /* flex:1/2; */
    margin: auto;
    height: 100vmin;
    width:100vmin;
    object-fit: cover;
    object-position: left 10%;
    /* transform: translate(0px, 10px); */
  }
  * {
    overflow-x:  hidden;
  }
}

@media screen and (min-width: 900px) {
  * {
    /* overflow: hidden ; */
  }
  
  h1 {
    font-size: 160px;
  }

  .subtitle {
    font-size: 130px;
  }

  h2{
    font-size: 90px;
  }
  h3{
    font-size: 50px;
  }

  section {
    height: 100vh;
    /* border:solid 1px black; */
    padding-top: 10vh;
  }

  .about {
    display: flex;
    flex-direction: row-reverse;
    margin:10px;
    /* border: 10px solid blue; */
    }
    
    .about img {
      flex:1/2;
      /* height: 70vh; */

      /* margin: null; */
      object-fit: contain;
      /* border: #61dafb 10px solid; */
      /* object-position: left 10%; */
    }
    
    .about-text {
      flex:2;
      /* border: solid 10px red */
      padding:0.5em;
      font-size: 0.8em;
    }
    
    .about-text span {
      font-size: 1.3em;
    }

    #projects img {
      width: 50vw;
      margin:auto;
    }
}